<template>

    <nav class="navbar-top">
        <div class="wrapper">
            <img src="../assets/images/logo50.png" alt="logo du site" class="logo">
            <input id="menu-toggler" type="checkbox"/>
            <label for="menu-toggler" class="navbar-mobile"><span></span><span></span><span></span></label>
            <ul class="navbar-list">
                <li class="navbar-list-item"><span class="navbar--list-link router-link-exact-active" to="/" data-name="Home"  @click.prevent="toggleMenu">Accueil</span></li>
                <li class="navbar-list-item"><span class="navbar--list-link" to="/realisations" data-name="Realisations" @click.prevent="toggleMenu">Réalisations</span></li>
                <li class="navbar-list-item"><span  class="navbar--list-link" to="#" name="" @click.prevent="toggleMenu">Â propos</span></li>
                <li class="navbar-list-item"><span class="navbar--list-link" to="#" name="" @click.prevent="toggleMenu">Contact</span></li>
                <li class="navbar-list-item"><span class="navbar--list-link" to="#" name="" @click.prevent="toggleMenu">Blog</span></li>
            </ul>
        </div>
    </nav>
</template>

<script>
    export default {
        name: "Navbar",
        mounted() {
            let navbar = document.querySelector('.navbar-top')
            if(this.scrollY > 100){
                navbar.style.backgroundColor = "rgb(0, 8, 43)"
            }
            else{
                navbar.style.backgroundColor = "transparent"

            }
            window.addEventListener('scroll', function () {
                if(this.scrollY > 100){
                    navbar.style.backgroundColor = "rgb(0, 8, 43)"
                }
                else{
                    navbar.style.backgroundColor = "transparent"
                }
            })
        },
        methods:{
            toggleMenu(event){
                document.querySelector('#menu-toggler').checked = false
                document.querySelectorAll('.navbar--list-link').forEach(ele=>{
                    ele.classList.remove('router-link-exact-active')
                })
                this.$router.push({name: event.target.dataset.name})
                event.target.classList.toggle('router-link-exact-active')
            }
        }
    }
</script>

<style lang="scss" scoped>
    .navbar-top{
        position: sticky;
        width: 100%;
        top: 0;
        z-index: 1002;
        transition: .3s;

    }

.navbar-top .wrapper{
    max-width: 1100px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5em 0;

    background-color: unset;
    border: none;
    img{
        width: 45px;
        height: 45px;
    }
    .navbar-list{
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        .navbar-list-item{
            padding: 0 1em;
            .navbar--list-link{
                text-decoration: none;
                color: white;
                font-size: .9em;
                font-weight: 300;
                position: relative;
                padding-bottom: 1em;
                font-family: 'Montserrat', sans-serif;
                &::after{
                    content: "";
                    position: absolute;
                    height: 3px;
                    left: 0;
                    bottom: 0;
                    width: 0;
                    background-color: #ffcd00;
                    transition: .3s;
                    opacity: 0;
                }
                &:hover{
                    color: #ffcd00;
                    &::after{
                        width: 100%;
                        opacity: 1;
                    }
                }
                &.router-link-exact-active{
                    color: #ffcb00;
                    &::after{
                        width: 100%;
                        opacity: 1;
                    }
                }
            }
        }
    }
    #menu-toggler{display: none}
    .navbar-mobile{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: none;
        background-color: transparent;
        width: 35px;
        height: 45px;
        display: none;
        span{
            display: inline-block;
            width: 95%;
            height: 4px;
            background-color: #ffcb00;
            margin-bottom: 4px;
        }
    }
}

    @media screen and (max-width: 786px){
        .navbar-top .wrapper{
            width: 100%;
            flex-wrap: wrap;
            font-size: 1.2em;
            padding: 1em;
            position: relative;
            .navbar-mobile{
                display: inline-flex;
                position: relative;
                z-index: 1001;
            }
            .navbar-list{
                order: 3;
                width: 100%;
                position: absolute;
                top: 0;
                left: 100%;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                height: 100vh;
                transform-origin: top;
                background-color: rgba(0, 8, 43, 0.89);
                z-index: 1000;
                padding: 0 2em;
                transition: .3s;
                .navbar-list-item{
                    padding: .5em;
                }
                .navbar--list-link{
                    &::after{display: none}
                }
            }
            #menu-toggler:checked {
                &~ .navbar-list {
                    left: 0;
                }
                &~ .navbar-mobile{
                    span{
                        transform-origin: center;
                        transition: .5s;
                    }
                    span:nth-of-type(2){
                        display: none;
                    }
                    span:nth-of-type(1){
                        transform: rotate(45deg) translateX(6px);
                    }
                    span:nth-of-type(3){
                        transform: rotate(-45deg) translateX(6px);
                    }
                }
            }
        }
    }

</style>